import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import DefaultAvatarLight from '../../svgs/icon-avatar-light.svg';
import DefaultAvatarDark from '../../svgs/icon-avatar-dark.svg';

import './index.scss';
import { uuidv4 } from '../../utils/uuid';

export const Img = ({
  src,
  height,
  width,
  alt,
}) => {
  const [realElement, setRealElement] = useState(null);
  // rerender element on src change
  useEffect(() => {
    setRealElement(null);
  }, [src]);
  return (
    realElement || (
      <img
        src={src}
        alt={alt}
        onError={() => {
          setRealElement((
            document.querySelector('.sendbird-theme--dark')
              ? <DefaultAvatarDark className="sendbird-avatar-img" style={{ height, width }} />
              : <DefaultAvatarLight className="sendbird-avatar-img" style={{ height, width }} />
          ));
        }}
        onLoad={() => {
          setRealElement((
            <div
              className="sendbird-avatar-img"
              style={{
                height,
                width,
                backgroundRepeat: 'no-repeat',
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundImage: `url(${src})`,
              }}
            />
          ));
        }}
      />
    )
  );
};

Img.propTypes = {
  src: PropTypes.string,
  height: PropTypes.string.isRequired,
  width: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
};

Img.defaultProps = {
  src: null,
};

export const AvatarInner = ({
  src,
  height,
  width,
  alt,
}) => {
  if (typeof src === 'string') {
    return (
      <Img src={src} height={height} width={width} alt={alt} />
    );
  }

  if (src && src.length) {
    if (src.length === 1) {
      return (
        <Img src={src[0]} height={height} width={width} alt={alt} />
      );
    }

    if (src.length === 2) {
      return (
        <div className="sendbird-avatar--inner__two-child">
          <Img src={src[0]} height={height} width={width} alt={alt} />
          <Img src={src[1]} height={height} width={width} alt={alt} />
        </div>
      );
    }

    if (src.length === 3) {
      return (
        <>
          <div className="sendbird-avatar--inner__three-child--upper">
            <Img src={src[0]} height={height} width={width} alt={alt} />
          </div>
          <div className="sendbird-avatar--inner__three-child--lower">
            <Img src={src[1]} height={height} width={width} alt={alt} />
            <Img src={src[2]} height={height} width={width} alt={alt} />
          </div>
        </>
      );
    }

    return (
      <div className="sendbird-avatar--inner__four-child">
        {
          src.slice(0, 4)
            .map((i) => (
              <Img src={i} height={height} width={width} alt={alt} key={uuidv4()} />
            ))
        }
      </div>
    );
  }
  // default img
  return <Img height={height} width={width} alt={alt} />;
};

AvatarInner.propTypes = {
  src: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  height: PropTypes.string.isRequired,
  width: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
};

AvatarInner.defaultProps = {
  src: '',
};

export default function Avatar({
  src,
  height,
  width,
  alt,
  className,
}) {
  const injectingClassName = (Array.isArray(className)) ? className : [className];
  return (
    <div
      className={
        [
          ...injectingClassName,
          'sendbird-avatar',
        ].join(' ')
      }
      style={{ height, width }}
    >
      <AvatarInner src={src} height={height} width={width} alt={alt} />
    </div>
  );
}

Avatar.propTypes = {
  src: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  height: PropTypes.string,
  width: PropTypes.string,
  alt: PropTypes.string,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
};

Avatar.defaultProps = {
  height: '56px',
  width: '56px',
  alt: '',
  src: '',
  className: '',
};
