import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './index.scss';

import Label, { LabelTypography, LabelColors } from '../Label';

import MenuItems_ from './items/MenuItems';
import ReactionItems_ from './items/ReactionItems';

export const MenuItems = MenuItems_;
export const ReactionItems = ReactionItems_;

export const MenuItem = ({ children, onClick }) => (
  <li
    tabIndex={0}
    className="sendbird-dropdown__menu-item"
    onClick={onClick}
    onKeyPress={(e) => {
      if (e.keyCode === 13) {
        onClick(e);
      }
    }}
    role="menuitem"
  >
    <Label
      type={LabelTypography.SUBTITLE_2}
      color={LabelColors.ONBACKGROUND_1}
      className="sendbird-dropdown__menu-item__text"
    >
      {children}
    </Label>
  </li>
);

MenuItem.propTypes = {
  onClick: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element,
  ]).isRequired,
};

export const MenuRoot = () => (
  <div id="sendbird-dropdown-portal" />
);

export default function ContextMenu({ menuTrigger, menuItems }) {
  const [showMenu, setShowMenu] = useState(false);

  return (
    <div style={{ display: 'inline' }}>
      {menuTrigger(() => setShowMenu(!showMenu))}
      {showMenu && menuItems(() => setShowMenu(false))}
    </div>
  );
}

ContextMenu.propTypes = {
  menuTrigger: PropTypes.func.isRequired,
  menuItems: PropTypes.func.isRequired,
};
